<script>
export default {
  name: "ItemThermography",
  props: {
    template: Object,
  },
  data() {
    return {
      itemsT: [],
      description: null,
      nom: null,
      value: null,
      items: null,
      model: null,
      overallPhoto: null,
      panel: null,
    };
  },
  watch: {
    value(e) {
      this.$emit("setOption", e);
    },
    overallPhoto(e) {
      console.log(e);
      this.$emit("setOverallPhoto", e);
    },
    /*  itemsT(e) {
      console.log(e);
      this.$emit("setItemsT", e);
    }, */
    itemsT: {
      deep: true,
      handler() {
        console.log(this.itemsT);
        this.$emit("setItemsT", this.itemsT);
      },
    },
    template() {
      this.start();
    },
  },
  created() {
    this.start();
  },
  methods: {
    start() {
      console.log("datps");
      console.log(this.template);
      this.description = this.template.description;
      this.nom = this.template.nom;
      this.value = this.template.value;
      this.items = this.template.items;
      this.model = this.template.model;
      this.overallPhoto = this.template.overallPhoto;
      if (this.template.itemsT) {
        this.itemsT = this.template.itemsT;
      }
    },
    addPoint() {
      const end = this.itemsT.length + 1;
      this.panel = this.itemsT.length;
      const newPoint = {
        number: end,
        specificPoint: "",
        photoNumber: "",
        specs: "",
      };
      this.itemsT.push(newPoint);
    },
    show() {
      console.log(this.itemsT);
      this.$emit("setItemsT", this.itemsT);
    },
    sentChangeItems() {
      console.log("change");
      //this.$emit("setItemsT", this.itemsT);
    },
    /*  change() {
